import React, { useCallback, useEffect, useMemo, useState } from "react";
import {
  BasicInfo,
  Expedition,
  Nav,
  Collection,
  NavContent,
  DoubleListContainer,
  Quality,
  Rune,
  Characteristic,
  Detail,
  ListItem,
  Dialog,
  Button,
  Text,
} from "components/";
import { useUser } from "hooks/use-user";
import * as Styled from "./index.style";

const UserInfo = ({
  match: {
    params: { name },
  },
}) => {
  const { userData, setUserData } = useUser();
  const [subNav, setSubNav] = useState(0);
  const [mainNav, setMainNav] = useState(0);
  const [dialog, setDialog] = useState(null);

  const collectionNav = useMemo(() => {
    if (!userData) return;
    return userData.collectionInfo.collectionMini.map((col, index) => (
      <Collection
        key={`collectionMini${index}`}
        index={index}
        size={col.size}
      />
    ));
  }, [userData]);

  const setMainNavHandler = useCallback(
    index => {
      setMainNav(index);
      setSubNav(0);
    },
    [setMainNav, setSubNav]
  );

  const expeditionDialog = useMemo(
    () => (
      <Expedition
        userData={userData}
        setUserData={setUserData}
        setDialog={setDialog}
      />
    ),
    [userData, setUserData, setDialog]
  );

  const setExpeditionDialog = useCallback(() => {
    setDialog(expeditionDialog);
  }, [setDialog, expeditionDialog]);

  useEffect(() => {
    return () => {
      setMainNav(0);
      setSubNav(0);
      setDialog(null);
    };
  }, [userData, setMainNav, setSubNav, setDialog]);

  useEffect(() => {
    if (!userData) setUserData(name);
  }, [name, setUserData, userData]);
  if (!userData) return null;

  const subNavs = [
    [" ", " ", ""],
    ["", ""],
    collectionNav,
  ];
  const mainNavs = ["", "", ""];
  const {
    characteristicInfo,
    equipInfo: { equipment, avatar },
  } = userData.abilityInfo;
  const { battleSkill, lifeSkill } = userData.skillInfo;
  const { collectionDetail } = userData.collectionInfo;
  const { battle, basic, engrave } = characteristicInfo;

  return (
    <>
      <Dialog dialog={dialog} setDialog={setDialog} />
      <Styled.Top>
        <Styled.ButtonContainer>
          <Button onClick={setExpeditionDialog}>
            <Text>  </Text>
          </Button>
        </Styled.ButtonContainer>
        <BasicInfo userData={userData} collection={collectionNav} />
      </Styled.Top>
      <Styled.Bottom>
        <Nav
          arr={mainNavs}
          isShow={true}
          selectedNav={mainNav}
          setNav={setMainNavHandler}
          navType="main"
        />
        {subNavs.map((tab, index) => (
          <Nav
            arr={tab}
            key={tab}
            isShow={mainNav === index}
            selectedNav={subNav}
            setNav={setSubNav}
            navType="sub"
          />
        ))}
        <Styled.Container>
          <NavContent selected={mainNav}>
            <NavContent selected={subNav}>
              <DoubleListContainer
                data={Object.values(equipment)}
                divideType="equip"
              >
                <ListItem setDialog={setDialog}>
                  <Detail>
                    <Quality />
                  </Detail>
                </ListItem>
              </DoubleListContainer>
              <DoubleListContainer
                data={Object.values(avatar)}
                divideType="inner"
              >
                <ListItem setDialog={setDialog}>
                  <Detail />
                </ListItem>
              </DoubleListContainer>
              <Characteristic data={[basic, battle, engrave]} />
            </NavContent>
            <NavContent selected={subNav}>
              <DoubleListContainer
                data={battleSkill.skills}
                lt={` : ${battleSkill.usePoint}`}
                rt={` : ${battleSkill.getPoint}`}
                divideType="leftSkill"
              >
                <ListItem setDialog={setDialog}>
                  <Detail>
                    <Rune />
                  </Detail>
                </ListItem>
              </DoubleListContainer>
              <DoubleListContainer data={lifeSkill} divideType="leftSkill">
                <ListItem />
              </DoubleListContainer>
            </NavContent>
            <NavContent selected={subNav}>
              {collectionDetail.map((res, index) => (
                <DoubleListContainer
                  key={index}
                  data={res.collection}
                  divideType="get"
                  lt={res.title}
                  rt={` : ${res.getCount}  : ${res.totalCount}`}
                >
                  <ListItem />
                </DoubleListContainer>
              ))}
            </NavContent>
          </NavContent>
        </Styled.Container>
      </Styled.Bottom>
    </>
  );
};

export default React.memo(UserInfo, () => true);
