import React from "react";
import { Image, Text } from "../";
import * as Styled from "./index.style";

const Rune = ({ data }: { data? }) => {
  const rune = data.detail.rune;

  if (!rune) return null;

  const { runeGrade, runeImg, runeInfo } = rune;

  let gradeColor = 0;
  if (runeGrade.includes("")) gradeColor = 1;
  if (runeGrade.includes("")) gradeColor = 2;
  if (runeGrade.includes("")) gradeColor = 3;
  if (runeGrade.includes("")) gradeColor = 4;
  if (runeGrade.includes("")) gradeColor = 5;

  return (
    <Styled.Container>
      <Image src={runeImg} color={`gradient${gradeColor}`} />
      <Styled.Desc>
        <Text color={`color${gradeColor}`}>{runeGrade}</Text>
        <Styled.FullText>
          <Text type="desc">{runeInfo}</Text>
        </Styled.FullText>
      </Styled.Desc>
    </Styled.Container>
  );
};

export default Rune;
